<template>
    <views v-if="isRender" class="prize">
        <input-info
            v-if="!showPrize"
            :imgs="imgObj"
            @submit="handleSubmit">
        </input-info>
        <views v-else>
            <disc-prize
                v-model='isPrizing'
                :gift-list="giftList"
                :result="prizeResult"
                :imgs="imgObj"
                @prize="handlePrize">
            </disc-prize>
        </views>
    </views>
</template>

<script>
import InputInfo from './discPrize/InputInfo'
import DiscPrize from './discPrize'
import tools from '@/mixins/tools'
// import storage from '@/utils/storage'
import api from '@/utils/api'
export default {
    name: 'Prize',
    components: {
        InputInfo,
        DiscPrize
    },
    mixins: [tools],
    data() {
        return {
            isRender: false,
            showPrize: false,
            form: {},
            giftList: [],
            isPrizing: false, // 是否抽奖中
            prizeResult: {}, // 抽奖结果
            imgObj: {
                prize1: require('@/assets/images/discPrize/prize_01.jpg'),
                prize2: require('@/assets/images/discPrize/prize_02.jpg'),
                prize3: require('@/assets/images/discPrize/prize_03.jpg'),
                prize4: require('@/assets/images/discPrize/prize_04.jpg'),
                prizePointer: require('@/assets/images/discPrize/zhizhen.png'),
                prizeRule: require('@/assets/images/discPrize/rules.png'),
                bg: require('@/assets/images/discPrize/result_bg.png'),
                contentBg: require('@/assets/images/discPrize/result_content_bg.png'),
                gift: require('@/assets/images/discPrize/gift.png'),
                addressBg: require('@/assets/images/discPrize/addres_bg.png')
            }
        }
    },
    computed: {
        query() {
            return this.$route.query
        }
    },
    beforeMount() {
        // if (!storage.get('accessToken')) return this.replace('/login')
        if (!this.query.public_code) return this.push('/verify?type=1&prize=1')
        if (!this.query.secret_code) return this.push(`/verify?type=2&prize=1&public_code=${this.query.public_code}`)
        this.isRender = true
        this.form.code_public = this.query.public_code
        this.form.code_secret = this.query.secret_code
        this.getPrizeList(111)
    },
    methods: {
        handleSubmit(obj) {
            Object.assign(this.form, obj)
            this.showPrize = true
        },
        handlePrize() {
            const params = {
                activity_id: '111',
                code_public: this.form.code_public,
                code_secret: this.form.code_secret,
                address: this.form.address,
                mobile: this.form.mobile,
                crop: this.form.crop,
                crop_area: this.form.crop_area
            }
            api.luckydraw(params).then((res) => {
                setTimeout(() => {
                    this.isPrizing = false
                    this.prizeResult = res
                    this.scrollToTop()
                }, 4200)
            })
        },
        getPrizeList(id) {
            api.getPrizeList({ id }).then((res) => {
                // this.giftList = res.list
                this.giftList = [...res.list, ...res.list, ...res.list, ...res.list, ...res.list, ...res.list, ...res.list]
            })
        }
    }
}
</script>
