<!-- 抽奖结果 -->

<template>
    <views class="prize-result">
        <images :src="imgs.bg"></images>
        <views class="prize-result-content">
            <images :src="imgs.contentBg"></images>
            <views class="inner">
                <views class="con">
                    <views class="li">
                        <images :src="imgs.gift"></images>
                    </views>
                    <views class="li title">{{ winTitle }}</views>
                    <views class="li name">{{ winName }}</views>
                    <views class="btn">
                        <button class="primary" @click="handlePrize">继续抽奖</button>
                    </views>
                    <views class="btn" >
                        <button @click="handleHome">返回首页</button>
                    </views>
                </views>
            </views>
        </views>
    </views>
</template>

<script>
import tools from '@/mixins/tools'
export default {
    mixins: [tools],
    props: {
        imgs: {
            type: Object,
            default: () => ({})
        },
        result: {
            type: Object,
            default: () => ({})
        }
    },
    data() {
        return {
            form: {}
        }
    },
    computed: {
        winTitle() {
            return this.result.isWin ? '恭喜您，中奖啦！' : '很遗憾，您没有中奖！'
        },
        winName() {
            return this.result.gift_name
        }
    },
    methods: {
        handleHome() {
            this.push('/')
        },
        handlePrize() {
            this.$emit('continuePrize')
            this.scrollToTop()
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
