<!-- 输入信息 -->

<template>
    <views class="input-info">
        <nav-bar
            title="惠农大抽奖"
            fixed
            theme="white"
            @click-left="onClickLeft">
        </nav-bar>
        <images :src="imgs.bg"></images>
        <views class="input-info-content">
            <images :src="imgs.contentBg"></images>
            <views class="inner">
                <views class="con">
                    <views class="title">请填写您的信息</views>
                    <views class="input-info-list">
                        <input v-model="form.mobile" type="number" pattern="[0-9]*" placeholder="请输入手机号">
                    </views>
                    <views class="input-info-list">
                        <input v-model="form.crop" type="text" placeholder="请输入种植作物">
                    </views>
                    <views class="input-info-list">
                        <input v-model="form.crop_area" type="number" pattern="[0-9]*" placeholder="请输入种植亩数">
                    </views>
                    <views class="input-info-list">
                        <button @click="handleSubmit">立即抽奖</button>
                    </views>
                </views>
            </views>
        </views>
    </views>
</template>

<script>
import NavBar from '@/components/navBar'
import tools from '@/mixins/tools'
export default {
    mixins: [tools],
    components: {
        NavBar
    },
    props: {
        imgs: {
            type: Object,
            default: () => ({})
        }
    },
    data() {
        return {
            form: {}
        }
    },
    methods: {
        onClickLeft() {
            this.push('/')
        },
        handleSubmit() {
            const { mobile, crop } = this.form
            if (!mobile) return this.notify('请输入手机号')
            if (!crop) return this.notify('请输入种植作物')
            if (!this.form.crop_area) return this.notify('请输入种植亩数')
            this.$emit('submit', this.form)
        },
        // 输入框获得焦点时，元素移动到可视区域
        inputOnFocus(e) {
            setTimeout(function() {
                e.target.scrollIntoView(true)
                // true:元素的顶端将和其所在滚动区的可视区域的顶端对齐; false:底端对齐。
            }, 200) // 延时 == 键盘弹起需要时间
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
